<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',

}
</script>

<style>
  @media screen and (max-width:641px) {
    .slide-enter,
    .slide_back-enter {
      position: absolute;
      width: 100%;
    }
    .slide-leave,
    .slide_back-leave {
      position: absolute;
      width: 100%;
      height:100%;
    }
    .slide-enter-active,
    .slide_back-enter-active {
      transition: all .3s ease;
    }
    .slide-leave-active {
      position: absolute;
      transition: all .3s ease;
      transform: translateX(-100%);
    }
    .slide-enter{
      transform: translateX(100%);
    }
    .slide_back-leave-active {
      position: absolute;
      transition: all .3s ease;
      transform: translate(100%);
    }
    .slide_back-enter {
      transform: translateX(-100%);
    }
  }
  body,ul,h3,ol,p{
        margin: 0;
        padding: 0;
      }
  body{
    font-family: '微软雅黑';
    font-size: 0.28rem;
    max-width: 640px;
    margin:0 auto;
  }
  ::-webkit-scrollbar{  /*不支持火狐*/
        display: none;
      }
  *{margin:0;padding:0;}
  li{list-style: none;}
  input { outline:0px ; -webkit-appearance: none; } 
  /*@media screen and (min-width:768px) {
      body {
          width:768px;
          margin:0 auto;
      }
      .footer{
          width:768px;
      }
      .hint{
         width:768px; 
      }
  }*/
</style>



