<template>
  	<div class="wrap">
  	    <div class="header">
              <h1>我的购物车<router-link to="/customer" tag="span"><img src="static/images/title.png" alt=""></router-link></h1>
              <div class="desc">
                  <span><img src="static/images/sure.png" alt="">欧美质量标准</span><span><img src="static/images/sure.png" alt="">30天无忧退货</span><span><img src="static/images/sure.png" alt="">24小时快速发货</span>
              </div>
          </div>
          <ul class="cart-box" v-for="(item,index) in data">
              <li><i class="check" @click="check(index)"><span class="shi" v-show="item.check"></span></i></li>
              <li><img :src="item.img" alt=""></li>
              <li class="cart-detail">
                  <p>{{item.title}}</p>
                  <p>颜色：{{item.color}}</p>
                  <p class="num"><span @click="reduce(index)">-</span><span>{{item.num}}</span><span @click="add(index)">+</span></p>
              </li>
              <img src="static/images/ls.png" alt="" class="right" @click="delog(index)">
              <li class="price"><p>￥ {{item.zo}}</p></li>   
          </ul>
          <div class="count">
              <i class="check" @click="all"><span class="shi" v-show="allshow"></span></i><span class="all">全选</span>
              <span class="sum">总计 （不含运费）：<span>￥ {{sum}}</span></span>
              <router-link to="/confirm" tag="button">立即下单</router-link>
          </div>
  		<shopfotNav></shopfotNav>
        <div class="wrap" v-show="dete">
          <div class="hint">
                <div class="hint-box">
                    <p>确定要删除这个商品吗？</p>
                    <p><span @click="delog">取消</span><span class="delete" @click="deteleok">删除</span></p>
                </div>
            </div> 
        </div>
    </div>
</template>
<script>
import shopfotNav from '@/components/shopfotNav'
export default {
	data(){
		return {
        indd:'',
        dete:false,
        allshow:0,
        zoprice:0,
        data:[{img:'/static/images/cart1.png',title:'Line高白玉骨瓷马克杯',color:'哑光绿',price:'169',check:0,num:1,zo:'169'},
              {img:'/static/images/cart2.png',title:'LED小台灯',color:'米白色',price:'69',check:0,num:1,zo:'69'},
        ]
		}
	},
  methods:{
    check(inde){
      this.data[inde].check=!this.data[inde].check;
    },
    add(inde){
      this.data[inde].num++;
      this.data[inde].zo=this.data[inde].price*this.data[inde].num;
    },
    reduce(inde){
      if (this.data[inde].num>1) {
          this.data[inde].num--;
          this.data[inde].zo=this.data[inde].price*this.data[inde].num;
      }
    },
    all(){
      this.allshow=!this.allshow;
      for (var i = 0; i < this.data.length; i++) {
        this.data[i].check=this.allshow;
      }
    },
    delog(inde){
      this.dete=!this.dete;
      this.indd=inde;
    },
    deteleok(){
      this.data.splice(this.indd,1)
      this.dete=false;
    },
  },
  computed:{
    sum(){
        var sum=0;
        for (var i = 0; i < this.data.length; i++) {
        if (this.data[i].check==1) {
          sum+=parseInt(this.data[i].zo);
        };
      };
        return sum;
    }
  },
	components:{
		shopfotNav,
	}
}
</script>
<style scoped>
  .wrap{background: #f9f9f9;position: relative;font-family: "微软雅黑" !important;}
  li{list-style: none;}
  .iconfont{font-size: .26rem;}
  .header{padding-top:.2rem;background: #fff;}
  .header h1{margin-bottom: .16rem;font-size: .32rem;text-align: center;position: relative;}
  .header h1 img{width:.36rem;vertical-align: middle;position: absolute;right:.32rem;}
  .desc{padding:0 .12rem; margin:.03rem 0;color:#6a6a6a;}
  .desc img{width:.3rem;height:.3rem;margin-right:.1rem; border-radius:50%; vertical-align: middle;}
  .desc span{display: inline-block; width:32.5%; font-size: .26rem;text-align: center;margin-bottom: .1rem;}
  .desc span:nth-of-type(3){width:35%;}
  .cart-box{height:2rem; position: relative;background: #fff;padding:.4rem .34rem;margin-bottom: .28rem;}
  .cart-box li{display: inline-block;font-size: .26rem}
  .cart-box .check{display: inline-block;width:.36rem;height:.36rem;border:.03rem solid #8b8b8b;border-radius: 50%;position:absolute;top:50%;transform: translateY(-50%);}
  .cart-box li img{width:1.6rem;height:1.6rem;margin:0 .18rem; border:1px solid #f9f9f9;border-radius:0.06rem;position:absolute;top:50%;transform: translateY(-50%);left:.8rem;}
  .cart-box .cart-detail{position: absolute;left:3rem;}
  .cart-detail p{margin-bottom:.32rem;}
  .cart-detail .num{display: inline-block; border-radius:.06rem;border:.02rem solid #8b8b8b;}
  .cart-detail .num span{display: inline-block;width:.62rem;height:.62rem;line-height: .62rem;text-align: center;}
  .cart-detail .num span:nth-of-type(2){border-left:.02rem solid #8b8b8b;border-right:.02rem solid #8b8b8b;}
  .cart-box .right{width:.34rem;height:.4rem;z-index: 99;position: absolute;right:.34rem;top:.4rem;}
  
  .cart-box .price{position: absolute;bottom:.4rem;right:.34rem;color:#fd888b;}
  .count{position:fixed;padding:.23rem .32rem; bottom:1.16rem;font-size:.26rem;background: #fff;width:100%;height:.68rem;max-width: 640px;}
  .count .check{margin-right:.2rem;display: inline-block;width:.36rem;height:.36rem;border:.03rem solid #8b8b8b;border-radius: 50%;vertical-align: middle;margin-top: .2rem;position: relative;}
  .shi{display: inline-block;position:absolute;width:.25rem;height:.25rem;background: #65c4aa;border-radius: 50%;top:50%;left:50%;margin-top:-.125rem;margin-left:-.125rem;}
  .count .all{vertical-align: middle;margin-top: .2rem;display: inline-block;}
  .count .sum{vertical-align: middle;margin-top: .2rem;display: inline-block;}
  .count .sum span{color:#fd888b;}
  .count button{display: inline-block;width:1.6rem;height:.8rem;border-radius: .08rem;color:white;background:#65c4aa; float: right;margin-right:.64rem;vertical-align: middle;border:none;outline: none;font-size: .26rem;}
  .fade-enter-active, .fade-leave-active {
      transition: opacity .5s
    }
    .fade-enter, .fade-leave-to{
      opacity: 0
    }
    .hint{top:0; width:100vw;height:100vh;background:rgba(22,22,22,0.4);position: fixed;z-index: 9999;}
    .hint .hint-box{width:4.3rem;height:2.32rem; position:absolute;top:50%;left:50%; transform: translate(-50%,-50%);font-size: .32rem;text-align: center;border-radius: .08rem;background: #fff;}
    .hint .hint-box p{height:49.5%;line-height:1.16rem;}
    .hint .hint-box p:nth-of-type(1){border-bottom: 1px solid #ececec;}
    .hint .hint-box p:nth-of-type(2) span{display: inline-block;width:49.5%;}
    .hint .hint-box p:nth-of-type(2) .delete{border-left: 1px solid #ececec;color:#fd888b;}
  </style>


