<template>
	<div>
		<div class="top-back">
			<img @click="back" src="static/images/goback.png">
		</div>
		<div class="photo">
		  <img src='static/images/people.png'>
			<p>登录</p>
		</div>
		<div class="reg_content">
			<div class="regisrer">
				<img src="static/images/phone.png">
				<li>
					<span>+86</span>
					<img src="static/images/stars_sjx.png">
					<input type="text" placeholder="输入手机号">
				</li>
			</div>
			<div class="scrod">
				<img src="static/images/lock.png">
				<span></span>
				<li>	
					<input type="password" placeholder="输入密码">
				</li>
			</div>
			<div class="confirm">确定</div>
			<p>使用验证码登录</p>
		</div>
		<div class="reg_foot">	
			<div >
				<p>使用社交账号登录</p>
			</div>
			<ul class="share">
					<router-link to="/shop" tag="li"><img src="static/images/weibo.png"><p>微博</p></router-link>
					<router-link to="/shop" tag="li"><img src="static/images/weixin.png"><p>微信</p></router-link>
					<router-link to="/shop" tag="li"><img src="static/images/qq.png"><p>QQ</p></router-link>
			</ul>
		</div>
	</div>

</template>
<script>
	export default{
		methods:{
			back(){
				window.history.go(-1)
			}
		},
		components:{
			
		},
	}
</script>
<style scoped>
	.top-back{
		width: 100%;
	}
	.top-back img{
		margin-top:0.3rem;margin-left:0.33rem;width:0.2rem;height: 0.35rem;
	}
	.photo{
		width:100%;height: 2.2rem;text-align: center;margin-top: 0.4rem;
	}
	.photo img{
		margin:0 auto;width: 1.45rem;height: 1.45rem;
	}
	.photo p{
		font-size: 0.36rem;color: black;margin-top:0.35rem;
	}
	.regisrer{
		height:0.9rem;width:5.86rem;margin:0 auto;margin-top:0.74rem;position: relative;
	}

	.regisrer img{
		position:absolute;top:0.3rem;left:0.08rem;width:0.3rem;height:0.38rem
	}
	.regisrer li{
		width: 5.11rem;height: 0.9rem;border-bottom: 1px solid gray;
		position: absolute;left:0.67rem;
	}
	.regisrer li>img{
		width:0.15rem;height: 0.1rem;position: absolute;left:0.6rem;top:0.4rem;
	}
	.regisrer span{
		position:absolute;left:-0.1rem;top:0.3rem;font-size: 0.3rem;line-height: 0.34rem;color:black;
	}
	.regisrer input{
		border:none;position:absolute; top:0.3rem;font-size: 0.3rem;left:0.9rem;border-left: .02rem solid #979797;text-indent: 0.16rem;outline: none;
	}
	
	.scrod{
		width:5.86rem;margin:0 auto;position:relative;height: 0.9rem;}
	.scrod img{
		position:absolute;top:0.3rem;left:0.08rem;width: 0.30rem;height: 0.38rem;
	}
	.scrod li{
		position:absolute; font-size: 0.34rem;left:0.67rem;
		 height: 0.9rem;border-bottom: 1px solid gray;width:5.15rem;
		
	}
	.scrod span{
		position: absolute;top:0.25rem;left:0.55rem;border-left:.02rem solid #979797;width: 0px;height: 0.46rem;

	}
	.scrod input{
		border:none;position: absolute;left:0.14rem ;font-size: 0.3rem;top:0.3rem;
	}
	.reg_content>p{
		width: 5.86rem;margin:0 auto;font-size: 0.25rem;margin-top: 0.4rem;text-align:right;}
	.reg_content{
		width: 100%;
	}
	.reg_foot{
		width: 100%;margin-top: 1.11rem;
	}
	.reg_foot div{
		width: 5.86rem;border-top:0.02rem solid #ececec;margin:0 auto;text-align:center;
	}
	.reg_foot div p{
		display: inline-block;background:white;height:.4rem;padding:0 .2rem; font-size: 0.26rem;text-align: center;margin:0 auto;letter-spacing: 0.06rem;margin-top:-0.15rem;position: relative;top:-.2rem;}

	 .confirm{
		width: 5.86rem;height:0.78rem;background: #b2e2d5;border-radius:0.08rem;margin:0 auto;margin-top:0.57rem;font-size: 0.34rem;text-align:center;line-height:0.8rem;color:white;
	}
	.share{
		margin:0 auto;
		margin-top:0.61rem;
		width: 5.4rem;
		height:0.94rem;
	

	}
	.share li{
		width:0.75rem;padding-left:0.5rem;padding-right:0.5rem;float: left;
		}
	.share li img{
		width: 0.75rem;height: 0.75rem;
	}
	
	.share p{
		text-align:
		center;font-size: 0.25rem;margin-top:0.18rem;
	}
	

</style>


