<template>
	<div class="wrap">
		<div class="header">
            <div>
                <div class="search">
                    <img src="/static/images/search.png" alt="" class="msg">
                    <input type="text" name="" class="outline" placeholder="全球优质供应商直供" v-model="inptext">
                </div>
                <router-link to="/search-about" class="send" v-show="inptext.length" @click="search" tag="span">发送</router-link>
                <router-link to="/message" tag="span" v-show="!inptext.length"><img src="/static/images/msg.png" alt="" class="msg"></router-link>
            </div>	
            <div class="nav">
                <router-link to="/shop/recommend" tag="li">推荐</router-link>
                <router-link to="/shop/decoration" tag="li">家饰</router-link>
                <router-link to="/shop/box" tag="li">收纳</router-link>
                <router-link to="/shop/kitchen" tag="li">餐厨</router-link>
                <router-link to="/shop/spin" tag="li">家纺</router-link>
            </div>
		</div>
	</div>
</template>
<script>
export default{
    data(){
        return{
            inptext:'',
        }
    },
    methods:{
        search(){
            // console.log(this.$route.params)
            this.inptext='';
        }
    },
    mounted(){

    }
}
</script>
<style scoped>
    .wrap{max-width: 640px;}
    .header{position: fixed;height:.88rem; width:100%;top:0;font-size:.24rem;background: #fff;z-index: 99;}
    .search{display: inline-block; height:.56rem;margin:.16rem .3rem;width:6.12rem;border-radius: .06rem;background: #ebebeb}
    .search img{height:.26rem;width:.26rem;margin:0 .22rem; line-height:.56rem;}
    .search input{height:100%;width:80%; background: transparent;font-size: .26rem;}
    .outline{outline: none;border:none;}
    .msg{width: .42rem;height:.34rem;margin-left:-.1rem; vertical-align: middle;}
    .send{width: .42rem;height:.34rem;margin-left:-.1rem;font-size: .26rem;color:#999; vertical-align: middle;}
    .nav{padding:0 .34rem;margin-top:-1px; font-size: .26rem;color:#999999;background: #fff;}
    .nav li{display: inline-block;height: .82rem;width:.75rem; margin-right: .65rem;line-height: .82rem;border-bottom:.08rem solid transparent;text-align: center;}
    .nav li:nth-last-of-type(1){margin:0}
    .active{color:#65c4aa;border-bottom:.08rem solid #65c4aa !important;}
</style>


